

.info-page {
  width: 100vw;
  height: 100vh;
  background: url('../images/background.jpeg') no-repeat center center;
  background-size: cover;
  text-align: center;
  font-family: 'Courier New', monospace;
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

h1 {
  font-size: 32px;
  margin-bottom: 20px;
}

p {
  font-size: 18px;
  margin: 10px 0;
}

.buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

button {
  padding: 10px 20px;
  font-size: 20px;
  border: 2px solid black;
  cursor: pointer;
  background-color: white;
  box-shadow: 3px 3px 0 black;
  transition: all 0.2s ease;
}

.safe-button {
  color: green;
}

.caution-button {
  color: yellow;
}

.danger-button {
  color: red;
}

button:hover {
  transform: translateY(-3px);
}

.back-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  border: 2px solid black;
  cursor: pointer;
  background-color: white;
  box-shadow: 3px 3px 0 black;
  transition: all 0.2s ease;
}

.back-button:hover {
  transform: translateY(-3px);
}
